<template>
    <nacl-page-container no-header>
        <!-- <nacl-header>我的收藏</nacl-header> -->
        <nacl-page-content id="collect">
            <ul>
                <li
                    v-for="item in listData"
                    :key="item.id"
                >
                    <a @click="goDetails('/'+item.valueId)">
                        <commodity :commodity-data="item" />
                    </a>
                </li>
            </ul>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import { collectList } from '@/services'
import commodity from '../components/commodity'

export default {
    name: 'collect',
    components: {
        commodity
    },
    data () {
        return {
            listData: []
        }
    },
    async created () {
        this.listData = await collectList()
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        }, 
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goDetails(param){
            this.toGetUser();
            window.webkit.messageHandlers.details.postMessage(param);
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
#collect {
  padding: 0 30px;
  background: #fff;
}
li {
  padding: 40px 0 50px;
  border-bottom: 2px solid #f2f2f2;
}
</style>
